:root {
    --primary: #add8e6;
    --primary-dark: #87ceeb;
    --secondary: #ffffff;
    --text-dark: #1a1a1a;
    --text-light: #555;
    --shadow: rgba(0, 0, 0, 0.1);
  }
  
  body {
    font-family: 'Inter', sans-serif;
    margin: 0;
    background-color: var(--secondary);
    color: var(--text-dark);
  }
  
  html {
    scroll-behavior: smooth;
  }
  
  .section {
    padding: 4rem 1rem;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .section-title {
    font-size: 2.2rem;
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .button {
    background-color: var(--primary);
    color: var(--text-dark);
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 5px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .button:hover {
    background-color: var(--primary-dark);
    transform: scale(1.03);
  }
  
  .card {
    background: var(--secondary);
    border-radius: 8px;
    box-shadow: 0 4px 6px var(--shadow);
    padding: 1.5rem;
    transition: transform 0.2s ease;
  }
  
  .card:hover {
    transform: translateY(-5px);
  }

  /* Responsive styles for screens smaller than 768px */
  @media (max-width: 768px) {
    .section {
      padding: 2rem 1rem; /* Reduce section padding */
    }
  
    .section-title {
      font-size: 1.8rem; /* Smaller font size for section titles */
    }
  
    .card {
      padding: 1rem; /* Reduce card padding */
    }
  
    .card h3 {
      font-size: 1.2rem; /* Smaller font size for card headings */
    }
  
    .card p {
      font-size: 0.95rem; /* Smaller font size for card text */
    }
  }
